.item-headers-comments {
  font-size: 25px;
  font-weight: bolder;
  margin-bottom: 5px;
}

.form-comment-container {
  width: 100%;
}

.form-div-comments {
  display: flex;
  justify-content: center;
}

.form-comments-format {
  padding: 5px 5px 5px 5px;
  border-style: outset;
}

.employee-name-info {
  width: fit-content;
}

.product-list-item-comment {
  margin-bottom: 0px;
  padding-top: 30px;
  padding-bottom: 35px;
  border-bottom: 5px solid #7a7a7a;
}

.item-info-comments {
  font-size: 18px;
  margin-top: 0px;
  padding-bottom: 20px;
  position: relative;
  display: inline-block;
  margin-left: 0px;
  margin-right: 0px;
  width: 100%;
  border-bottom: 1px solid #7a7a7a;
}

.item-info-bottom-comments {
  font-size: 20px;
  margin-top: 0px;
  padding-bottom: 20px;
}

.item-info-box-comments {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  margin-left: 15px;
  padding-left: 0px;
  width: 900px;
  margin-top: 18px;
  margin-left: 0px;
}

.info-input-comments {
  font-size: 18px;
  padding-bottom: 8px;
  margin-bottom: 8px;
  width: 300px;
}

.comment-submit-button {
  width: 300px;
  justify-content: center;
}

.comment-header {
  font-size: 20px;
  color: black;
  font-weight: bold;
}

.comment-adjust-buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.read-by-text {
    font-size: 25px;
    color: red;
}

